<template>
  <div id="about">
    <div class="wrapper">
      <Title v-bind:text="title"></Title>
      <div class="cards-wrapper">

      </div>
    </div>
  </div>
</template>

<style>
#home .wrapper {
  max-width:1000px;
  margin:40px auto;
  padding: 0 16px;
}

.cards-wrapper {
  margin-bottom: 20px;
  display: inline-block;
  width: 100%

}
</style>

<script>
import Card from './reuseable/Card'
import Title from './reuseable/Title'

export default {
  data () {
    if (window.localStorage.language === 'chi') {
      return {
        title: '春晖制造'
      }
    } else {
      return {
        title: 'Manufacturing'
      }
    }
  },
  components: {
    Card,
    Title
  }
}
</script>



